.card {
  width: $column-width;
  height: 408px;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  border: 1px solid $ivory-color;
  border-radius: 5px;
  margin: 0;
  padding: 32px 16px 16px;
  transition: all 0.3s ease-in-out;

  &:hover {
    transform: scale(1.2);
  }

  &__image {
    height: 134px;
    align-self: center;
    margin-bottom: 40px;
  }

  &__title {
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    margin-bottom: 4px;
    color: $dark-grey-color;
  }

  &:hover &__title {
    color: $deep-blue-color;
  }

  &__code {
    font-size: 10px;
    line-height: 14px;
    color: $light-grey-color;
    margin-bottom: 16px;
  }

  &__review {
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;
    align-items: center;

    &-value {
      font-size: 10px;
      line-height: 14px;
      color: $dark-grey-color;
    }
  }

  &__button {
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    background-color: $light-blue-color;
    color: $white-color;
    text-transform: uppercase;
    text-decoration: none;
    text-align: center;
    border-radius: 5px;
    border: none;
    padding: 12px;
    transition: all 0.3s ease-in-out;

    &:hover {
      background-color: $white-color;
      color: $light-blue-color;
      border: 1px solid $light-blue-color;
    }
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;

    &-name {
      margin: 0;
      font-size: 12px;
      line-height: 18px;
      color: $light-grey-color;
    }

    &-value {
      margin: 0;
      font-weight: 700;
      font-size: 16px;
      line-height: 18px;
      color: $dark-grey-color;
    }
  }
}
